<template>
	<view class="content">
		<view class="upload_text">
			<textarea v-model="text_valeue" placeholder-style="color:#C9C9C9" maxlength="140" placeholder="写下此刻的想法…"/>
			<text>{{text_valeue.length}}/140</text>
		</view>
		<view class="pic_list">
			<image v-for="(n, index) in img_url" :src="n" mode="widthFix" :key="index"></image>
			<image @click="upload_img" v-if="img_url.length < 6" src="../../static/photo.png" mode="widthFix"></image>
			<view class="add_photo">
			</view>
		</view>
	</view>
</template>

<script>
	import moment from 'moment'
	import _ from 'lodash'
	export default {
		data() {
			return {
				text_valeue: '',
				img_url: []
			}
		},
		onShow() {
			
		},
		onLoad(option){
		},
		onNavigationBarButtonTap: function(e) {
			if (this.text_valeue) {
				this.$http
				.post('/api/adddynamic', {
					userid: uni.getStorageSync('userId'),
					content: this.text_valeue,
					imagelist: this.img_url
				}, {})
				.then(res => {
					this.$api.msg('发布成功')
					uni.navigateBack({
					    delta: 1,
					    animationType: 'pop-out',
					    animationDuration: 500
					});
				})
			} else {
				this.$api.msg('请添加文字描述')
			}
		},
		methods: {
			submit_video () {
			},
			upload_img () {
				let _this = this
				let count = 6 - this.img_url.length
				let tiem = moment().format('YYYY-MM-DD')
				let ss = moment().format('x')
				let userId = uni.getStorageSync('userId')
				let mobile = '17635532823'
				uni.chooseImage({
				    count: count, //默认9
				    sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				    success: function (res) {
						console.log(res)
						if (res.errMsg === "chooseImage:ok") {
							_.each(res.tempFiles, o => {
								let arr = []
								arr = o.name.split('.')
								let img_type = arr[arr.length - 1]
								let img_key = 'xiangqin/' + tiem + '/' + mobile + '/' + userId + '_' + ss + '_' + arr[0] + '_pic.' + img_type
								console.log(img_key)
								uni.uploadFile({
									url: 'https://qilianxiangqin.oss-cn-beijing.aliyuncs.com', 
									fileType: "image",
									filePath: o.path,
									name: 'file',
									formData: {
										name: o.name,
										key: img_key, // 文件名
										policy: 'eyJleHBpcmF0aW9uIjoiMjAyMS0wMS0wMVQxMjowMDowMC4wMDBaIiwiY29uZGl0aW9ucyI6W1siY29udGVudC1sZW5ndGgtcmFuZ2UiLDAsMTA0ODU3NjAwMF1dfQ==',
										OSSAccessKeyId: 'LTAI4G2aMnRWAq1TL6YQGv39',
										success_action_status: 200,
										signature: '7fESSx7D/GZ67J+junaMkigCQ38='
									},
									success: (uploadFileRes) => {
										let url_str = 'https://qilianxiangqin.oss-cn-beijing.aliyuncs.com/' + img_key
										_this.img_url.push(url_str)
									}
								});
							})
						}
				    }
				});
			}
		}  
	}
</script>

<style lang="scss">
	page{
		background: #fff;
		height: 100%;
	}
	.content{
		height: 100%;
		padding-top: 10upx;
	}
	.upload_text{
		width: 95%;
		margin: 20upx auto 0;
		height: 340upx;
		display: flex;
		justify-content: center;
		position: relative;
		textarea{
			width: 95%;
			color: #444;
			height: 100%;
		}
		text{
			font-size: 20upx;
			position: absolute;
			right: 20upx;
			bottom: 20upx;
			color: #C9C9C9;
		}
	}
	.pic_list{
		width: 704upx;
		margin: 10upx auto;
		display: flex;
		flex-wrap: wrap;
		image{
			width: 228upx;
			height: 228upx;
			margin-right: 10upx;
			margin-bottom: 10upx;
		}
		image:nth-child(3n){
			margin-right: 0;
		}
		add_photo{
			
		}
	}
</style>
